'use strict';

var RLP = require('rlp');

var multicodec = require('multicodec');

var ipldEthBlock = require('../eth-block');

var createResolver = require('../util/createResolver');

var deserialize = function deserialize(serialized) {
  var rawOmmers = RLP.decode(serialized);
  var deserialized = rawOmmers.map(function (rawBlock) {
    return ipldEthBlock.util.deserialize(rawBlock);
  });
  deserialized.count = deserialized.length;
  return deserialized;
};

var ethBlockListResolver = createResolver(multicodec.ETH_BLOCK_LIST, deserialize);

ethBlockListResolver.util.serialize = function (ethBlockList) {
  var rawOmmers = ethBlockList.map(function (ethBlock) {
    return ethBlock._ethObj.raw;
  });
  return RLP.encode(rawOmmers);
};

module.exports = ethBlockListResolver;