'use strict';

var multihashes = require('multihashes/src/constants');

var multihash = require('multihashes');

var CID = require('cids');

var strftime = require('strftime');

exports = module.exports;
exports.SHA1_LENGTH = multihashes.defaultLengths[multihashes.names.sha1];

exports.find = function (buf, _byte) {
  for (var i = 0; i < buf.length; i++) {
    if (buf[i] === _byte) {
      return i;
    }
  }

  return -1;
};

var ISO_8601_STRICT = '%FT%T%:z';
var TIMESTAMP_WITH_OFFSET = '%s %z';

var timestampWithOffsetToISOStrict = function timestampWithOffsetToISOStrict(timestamp, offset) {
  return strftime.timezone(offset)(ISO_8601_STRICT, new Date(timestamp * 1000));
};

var isoStrictToTimestampWithOffset = function isoStrictToTimestampWithOffset(isoString) {
  var matched = isoString.match(/([+-]\d{2}:\d{2})/);
  var offset = matched === null ? '+0000' : matched[0].slice(0, 3) + matched[0].slice(4);
  return strftime.timezone(offset)(TIMESTAMP_WITH_OFFSET, new Date(isoString));
};

exports.parsePersonLine = function (line) {
  var matched = line.match(/^(([^<]+)\s)?\s?<([^>]+)>\s?(?:(\d+)\s([+-]\d+))?$/);

  if (matched === null) {
    return null;
  }

  return {
    name: matched[2],
    email: matched[3],
    date: matched[4] && matched[5] && timestampWithOffsetToISOStrict(parseInt(matched[4]), matched[5])
  };
};

exports.serializePersonLine = function (node) {
  var parts = [];

  if (node.name) {
    parts.push(node.name);
  }

  parts.push('<' + node.email + '>');

  if (node.date) {
    parts.push(isoStrictToTimestampWithOffset(node.date));
  }

  return parts.join(' ');
};

exports.shaToCid = function (buf) {
  var mh = multihash.encode(buf, 'sha1');
  return new CID(1, 'git-raw', mh);
};

exports.cidToSha = function (cid) {
  var mh = multihash.decode(cid.multihash);

  if (mh.name !== 'sha1') {
    return null;
  }

  return mh.digest;
};